import Tools from './tools/ToolsView';
import FrameManager from './FrameManagerView';
import Canvas from './canvas/CanvasView';
import Preview from './preview/PreviewView';
import './app-view.css';

export default class AppView {
  constructor() {
    this.data = [];
    this.currentTool = null;
  }

  // eslint-disable-next-line class-methods-use-this
  render() {
    const body = document.querySelector('body');
    const wrapper = document.createElement('div');
    wrapper.classList.add('wrapper');
    body.appendChild(wrapper);
    const tools = new Tools();
    tools.add();
    this.currentTool = tools.currentTool;
    const frames = new FrameManager(this.data);
    frames.add();
    const canvas = new Canvas(frames.data, this.currentTool);
    canvas.add();
    const preview = new Preview(this.data);
    preview.add();

    // eslint-disable-next-line no-console
    // console.log(' --', this.currentTool);
  }
}
