import './canvas-view.css';
import CanvasItem from './CanvasItem';

export default class Canvas {
  constructor(data, currentTool) {
    this.data = data;
    this.currentTool = currentTool;
  }

  // eslint-disable-next-line class-methods-use-this
  add() {
    const wrapper = document.querySelector('.wrapper');
    const canvas = document.createElement('div');
    canvas.classList.add('canvas-wrapper');
    wrapper.appendChild(canvas);

    const canvasItem = new CanvasItem(this.data, this.currentTool);
    this.data.canvasItemLink = canvasItem;
    canvasItem.add();
    // eslint-disable-next-line no-console
    // console.log('addCanvas', this.data);
  }
}
