import './frameManager-view.css';
import Frame from './frames/ItemFrame';

export default class FrameManager {
  constructor(data) {
    this.data = data;
  }

  render() {
    // eslint-disable-next-line no-console
    // console.log('rend -- ', this.data);
    const frameManager = document.querySelector('.frameManager');
    frameManager.innerHTML = '';

    const btnAddNewFrame = document.createElement('div');
    btnAddNewFrame.classList.add('frameManager-btn_addNew');
    btnAddNewFrame.innerHTML = '+ Add new frame';
    btnAddNewFrame.addEventListener('click', this.addFrame.bind(this));
    frameManager.appendChild(btnAddNewFrame);

    this.data.map((item, index) => {
      this.data[index].number = index;
      item.add(this.data, index, this);
      item.viewCurrentFrameInFrameCanv(index);
      // eslint-disable-next-line no-console
      // console.log('render --', this.data[index].coords);
      // const serialData = JSON.stringify(this.data[index].coords);
      // eslint-disable-next-line prefer-template
      // localStorage.setItem('data' + index, serialData);
      // localStorage.clear();
      return item;
    });
  }

  // eslint-disable-next-line class-methods-use-this
  duplicateFrame() {
    const frame = new Frame();
    this.data.forEach((item) => {
      if (item.current === true) {
        item.coords.forEach((itemc) => {
          frame.coords.push(itemc);
        });
      }
    });
    this.data.push(frame);
    // eslint-disable-next-line no-console
    // console.log('duplicate Frame ', this.data);
  }

  // eslint-disable-next-line class-methods-use-this
  addFrame() {
    if (this.data.canvasItemLink) {
      this.data.canvasItemLink.clearCanvas();
    }
    this.data.map((item, index) => {
      if (item.current !== false) {
        this.data[index].current = false;
      }
      // // eslint-disable-next-line no-console
      // console.log('item', item.current);
      return item;
    });

    const frame = new Frame();
    this.data.push(frame);
    this.render.bind(this);
    this.render();
    // eslint-disable-next-line no-console
    // console.log('length -- ', this.data.canvasItemLink);
  }


  // eslint-disable-next-line class-methods-use-this
  add() {
    const wrapper = document.querySelector('.wrapper');
    const frameManager = document.createElement('div');
    frameManager.classList.add('frameManager');
    wrapper.appendChild(frameManager);

    const btnAddNewFrame = document.createElement('div');
    btnAddNewFrame.classList.add('frameManager-btn_addNew');
    btnAddNewFrame.innerHTML = '+ Add new frame';
    btnAddNewFrame.addEventListener('click', this.addFrame.bind(this));
    frameManager.appendChild(btnAddNewFrame);
    this.addFrame();
    // eslint-disable-next-line no-console
    // console.log('addFrameManager');
  }
}
