import Pen from './ToolsPen';
import Eraser from './ToolsEraser';
import './tools-view.css';
import ColorSelect from './ToolsColorSelect';

export default class Tools {
  constructor() {
    this.currentTool = {
      curTool: null,
      primaryColor: 'black',
      secondaryColor: 'red',
    };
    this.add = () => {
      const wrapper = document.querySelector('.wrapper');
      const tools = document.createElement('div');
      tools.classList.add('tools');
      wrapper.appendChild(tools);
      const pen = new Pen(this.currentTool);
      pen.add();
      const eraser = new Eraser(this.currentTool);
      eraser.add();
      const colorSelect = new ColorSelect(this.currentTool);
      colorSelect.add();
    };
  }
}
