import './preview-view.css';
import PreviewItem from './PreviewItem';

export default class Preview {
  constructor(data) {
    this.data = data;
  }


  // eslint-disable-next-line class-methods-use-this
  fullscreen() {
    // eslint-disable-next-line no-console
    // console.log('fullscreen');
    const previewWrap = document.querySelector('.preview-wrap');
    previewWrap.classList.toggle('preview-wrap-fullscreen');
  }

  // eslint-disable-next-line class-methods-use-this
  add() {
    const wrapper = document.querySelector('.wrapper');
    const previewWrap = document.createElement('div');
    previewWrap.classList.add('preview-wrap');
    wrapper.appendChild(previewWrap);
    const btnFullscreen = document.createElement('div');
    btnFullscreen.classList.add('preview-btn-full-screen');
    btnFullscreen.innerHTML = '<i class="fas fa-compress"></i>';
    btnFullscreen.addEventListener('click', this.fullscreen);
    previewWrap.appendChild(btnFullscreen);
    const previewItem = new PreviewItem(this.data);
    previewItem.add();
    let count = 0;
    // eslint-disable-next-line no-console
    // console.log('goPreview');
    setInterval(() => {
      if (count === this.data.length) {
        count = 0;
      }
      previewItem.play(count);
      // eslint-disable-next-line no-console
      // console.log('go', count, this.data[count].coords);
      count += 1;
    }, 300);
    // eslint-disable-next-line no-console
    // console.log('data view -- ', this.data[0]);
  }
}
