import './tools-pen-view.css';

export default class Pen {
  constructor(current) {
    this.state = null;
    this.currentTool = current;
    this.toBeCurrent = () => {
      this.currentTool.curTool = 'pen';
      if (this.currentTool.curTool === 'pen') {
        const tools = document.querySelectorAll('.tools-item');
        tools.forEach((item) => {
          item.classList.remove('tools-current');
        });
        const pen = document.querySelector('.tools-pen');
        pen.classList.add('tools-current');
      }
    };
    this.add = () => {
      const toolsWrap = document.querySelector('.tools');
      const pen = document.createElement('div');
      pen.classList.add('tools-item');
      pen.classList.add('tools-pen');
      pen.innerHTML = '<i class="fas fa-pen"></i>';
      toolsWrap.appendChild(pen);
      pen.addEventListener('click', this.toBeCurrent);
      this.toBeCurrent();
      // eslint-disable-next-line no-console
      // console.log('current tool == ');
    };
  }
}
