import './preview-item.css';

export default class PreviewItem {
  constructor(data) {
    this.data = data;
  }

  // eslint-disable-next-line class-methods-use-this
  clear() {
    const canv = document.querySelector('.preview-canvas');
    const ctx = canv.getContext('2d');
    ctx.fillStyle = '#808080';
    ctx.fillRect(0, 0, canv.width, canv.height);

    ctx.beginPath();
    ctx.fillStyle = 'black';
  }

  play(index) {
    // eslint-disable-next-line no-console
    // console.log('play Preview --', this.data, index);
    this.clear();
    if (this.data[index].coords) {
      this.data[index].coords.map((itemC) => {
        itemC.drawPreview();
        // eslint-disable-next-line no-console
        console.log('replay', itemC);
        return itemC;
      });
    }
  }

  // eslint-disable-next-line class-methods-use-this
  add() {
    const previewWrap = document.querySelector('.preview-wrap');
    const previewCanvas = document.createElement('canvas');
    previewCanvas.width = 450;
    previewCanvas.height = 450;
    previewCanvas.classList.add('preview-canvas');
    previewWrap.appendChild(previewCanvas);
  }
}
