import './tools-eraser-view.css';

export default class Eraser {
  constructor(current) {
    this.state = null;
    this.currentTool = current;
    this.toBeCurrent = () => {
      this.currentTool.curTool = 'eraser';
      if (this.currentTool.curTool === 'eraser') {
        const tools = document.querySelectorAll('.tools-item');
        tools.forEach((item) => {
          item.classList.remove('tools-current');
        });
        const eraser = document.querySelector('.tools-eraser');
        eraser.classList.add('tools-current');
      }
    };
    this.add = () => {
      const toolsWrap = document.querySelector('.tools');
      const eraser = document.createElement('div');
      eraser.classList.add('tools-item');
      eraser.classList.add('tools-eraser');
      eraser.addEventListener('click', this.toBeCurrent);
      eraser.innerHTML = '<i class="fas fa-eraser"></i>';
      toolsWrap.appendChild(eraser);
    };
  }
}
