import './canvas-item.css';
import Rect from './rectForCanvas';

export default class CanvasItem {
  constructor(data, currentTool) {
    this.data = data;
    this.currentTool = currentTool;
    this.isMouseDown = false;
    this.color = this.currentTool.primaryColor;
    this.deltaX = null;
    this.deltaY = null;
    this.width = 450;
    this.height = 450;
    this.counPixel = 32;
    this.widthPixel = this.width / this.counPixel;
    this.map = [];
    this.add = () => {
      const canvasWrap = document.querySelector('.canvas-wrapper');
      const canv = document.createElement('canvas');
      canv.classList.add('canvas-item');
      canv.width = this.width;
      canv.height = this.height;
      canvasWrap.appendChild(canv);
      // this.createMap();
      canv.addEventListener('mousedown', this.mouseDown.bind(this));
      canv.addEventListener('mouseup', this.mouseUp.bind(this), false);
      canv.addEventListener('mousemove', this.mouseMove.bind(this));
      canv.addEventListener('contextmenu', event => event.preventDefault());
      // document.addEventListener('mousemove', this.move);
      // let counterLocalItem = 0;
      // let localstorage = true;

      // while (localstorage === true) {
      //   // eslint-disable-next-line prefer-template
      //   if (localStorage.getItem('data' + counterLocalItem)) {
      //     // eslint-disable-next-line prefer-template
      //     const localCoords = JSON.parse(localStorage.getItem('data' + counterLocalItem));
      //     // const realcoords = [];
      //     // eslint-disable-next-line operator-assignment
      //     counterLocalItem = counterLocalItem + 1;
      //     localCoords.forEach((item) => {
      //       const rect = new Rect(item.color, item.x, item.y, item.width, item.height);
      //       this.map.push(rect);
      //     });
      //     // this.map.push(realcoords);
      //     // this.data[0].link.addFrame(realcoords);
      //   } else {
      //     localstorage = false;
      //   }
      // }
      // const map = JSON.stringify(this.map);
      // localStorage.setItem('map', map);
      // // eslint-disable-next-line no-console
      // console.log('map -- ', this.map);

      // if (localStorage.getItem('data1')) {
      //   const localCoords = JSON.parse(localStorage.getItem('data1'));
      //   const realcoords = [];
      //   localCoords.forEach((item) => {
      //     const rect = new Rect(item.color, item.x, item.y, item.width, item.height);
      //     realcoords.push(rect);
      //   });
      //   this.data[0].link.addFrame(realcoords);
      //   // eslint-disable-next-line no-console
      //   console.log('locStorage data', this.data[0].link, localCoords, realcoords);
      // }

      // eslint-disable-next-line no-console
      // console.log(' canvItem data-- ', this.data);
    };
    this.clearCanvas = () => {
      const canv = document.querySelector('.canvas-item');
      const ctx = canv.getContext('2d');
      ctx.fillStyle = '#808080';
      ctx.fillRect(0, 0, canv.width, canv.height);
      ctx.beginPath();
      ctx.fillStyle = 'black';
      // eslint-disable-next-line no-console
      console.log('clearCanvas');
    };
  }

  viewCurrentFrameInCanvas() {
    this.clearCanvas();
    this.data.map((item) => {
      if (item.current === true) {
        // eslint-disable-next-line no-console
        // console.log('replay');
        item.coords.map((itemC) => {
          itemC.draw();
          // eslint-disable-next-line no-console
          // console.log('replay itemC', itemC[0]);
          return itemC;
        });
      }
      return item;
    });
  }

  pushCoords(coor) {
    this.data.map((item) => {
      if (item.current === true) {
        item.coords.push(coor);
        this.data[0].link.render();
        // eslint-disable-next-line no-console
        // console.log('push --', item.coords, coor);
      }
      return item;
    });
  }

  mouseDown(e) {
    let color = null;
    if (e.buttons === 1) {
      color = this.currentTool.primaryColor;
    }
    if (e.buttons === 2) {
      color = this.currentTool.secondaryColor;
    }
    const canv = document.querySelector('.canvas-item');
    const ctx = canv.getContext('2d');
    ctx.beginPath();
    this.isMouseDown = true;
    const x = (Math.floor((e.clientX - canv.offsetLeft) / this.widthPixel) * this.widthPixel);
    const y = (Math.floor((e.clientY - canv.offsetTop) / this.widthPixel) * this.widthPixel);

    if (this.isMouseDown && this.currentTool.curTool === 'pen') {
      const rect = new Rect(color, x, y, canv.width / 32, canv.height / 32);
      this.pushCoords(rect);
      rect.draw();
      // eslint-disable-next-line no-console
      // console.log('mouse Down -- ', e, this.currentTool);
    }

    if (this.isMouseDown && this.currentTool.curTool === 'eraser') {
      this.data.forEach((itemq) => {
        if (itemq.current === true) {
          // eslint-disable-next-line no-console
          console.log('item current -- ==========', itemq.number, this.data[itemq.number]);
          let counter = 0;
          this.data[itemq.number].coords.forEach((itemCords) => {
            // eslint-disable-next-line operator-assignment
            counter = counter + 1;
            if (x === itemCords.x && y === itemCords.y) {
              // eslint-disable-next-line no-param-reassign
              itemCords.color = '#808080';
              this.viewCurrentFrameInCanvas();
              // item.coords.splice(counter, 1);
              counter = 0;
              // eslint-disable-next-line no-console
              // console.log('eraser Down item.coords.x', item.coords, itemCords.x, x, counter);
            }
            // eslint-disable-next-line no-console
            // console.log('itemCords', itemCords);
          });
          // eslint-disable-next-line no-console
          // console.log('item eraser dawn', item.coords);
        }
      });
    }
  }

  mouseUp() {
    this.isMouseDown = false;
    const canv = document.querySelector('.canvas-item');
    const ctx = canv.getContext('2d');
    ctx.beginPath();
    // this.pushCoords('mouseup');
    // eslint-disable-next-line no-console
    // console.log('mouse UP MAP -- ', this.map);
  }

  // eslint-disable-next-line class-methods-use-this
  mouseMove(e) {
    let color = null;
    if (e.buttons === 1) {
      color = this.currentTool.primaryColor;
    }
    if (e.button === 2) {
      color = this.currentTool.secondaryColor;
    }
    const canv = document.querySelector('.canvas-item');
    // const ctx = canv.getContext('2d');
    // const coordsMove = [(e.clientX - canv.offsetLeft) * 2, (e.clientY - canv.offsetTop)];
    const x = (Math.floor((e.clientX - canv.offsetLeft) / this.widthPixel) * this.widthPixel);
    const y = (Math.floor((e.clientY - canv.offsetTop) / this.widthPixel) * this.widthPixel);
    if (this.isMouseDown && this.currentTool.curTool === 'pen') {
      const rect = new Rect(color, x, y, canv.width / 32, canv.height / 32);
      this.pushCoords(rect);
      rect.draw();
      // eslint-disable-next-line no-console
      // console.log('move -', e);
    }
    if (this.isMouseDown && this.currentTool.curTool === 'eraser') {
      this.data.forEach((itemq) => {
        if (itemq.current === true) {
          // eslint-disable-next-line no-console
          console.log('item current -- ==========', itemq.number, this.data[itemq.number]);
          let counter = 0;
          this.data[itemq.number].coords.forEach((itemCords) => {
            // eslint-disable-next-line operator-assignment
            counter = counter + 1;
            if (x === itemCords.x && y === itemCords.y) {
              // eslint-disable-next-line no-param-reassign
              itemCords.color = '#808080';
              this.viewCurrentFrameInCanvas();
              // item.coords.splice(counter, 1);
              counter = 0;
              // eslint-disable-next-line no-console
              // console.log('eraser Down item.coords.x', item.coords, itemCords.x, x, counter);
            }
            // eslint-disable-next-line no-console
            // console.log('itemCords', itemCords);
          });
          // eslint-disable-next-line no-console
          // console.log('item eraser dawn', item.coords);
        }
      });
      this.data[0].link.render();
    }
  }
}
