import './tools-colorSelect-view.css';

export default class ColorSelect {
  constructor(currentTool) {
    this.currentTool = currentTool;
    this.currentColorSecondary = (e) => {
      const style = getComputedStyle(e.target);
      this.currentTool.secondaryColor = style.backgroundColor;
      const secondaryColor = document.querySelector('.tools-secondaryColor');
      secondaryColor.style.backgroundColor = style.backgroundColor;
      secondaryColor.classList.remove('selecting');
      const colorSelect = document.querySelector('.tools-colorSelect');
      const colorsWrap = document.querySelector('.tools-colorsWrap');
      colorSelect.removeChild(colorsWrap);
      // eslint-disable-next-line no-console
      console.log('clor e-', e.target, style.backgroundColor, this.currentTool);
    };
    this.currentColorPrimary = (e) => {
      const style = getComputedStyle(e.target);
      this.currentTool.primaryColor = style.backgroundColor;
      const primaryColor = document.querySelector('.tools-primaryColor');
      primaryColor.style.backgroundColor = style.backgroundColor;
      primaryColor.classList.remove('selecting');
      const colorSelect = document.querySelector('.tools-colorSelect');
      const colorsWrap = document.querySelector('.tools-colorsWrap');
      colorSelect.removeChild(colorsWrap);
      // eslint-disable-next-line no-console
      console.log('clor e-', e.target, style.backgroundColor, this.currentTool);
    };
    this.selectColorSecondary = () => {
      const colorSelect = document.querySelector('.tools-colorSelect');
      const colorsWrap = document.createElement('div');
      const secondaryColor = document.querySelector('.tools-secondaryColor');
      secondaryColor.classList.add('selecting');
      colorsWrap.classList.add('tools-colorsWrap');
      const colorRed = document.createElement('div');
      colorRed.classList.add('tools-color-item');
      colorRed.style.backgroundColor = '#FF0000';
      colorRed.addEventListener('click', this.currentColorSecondary);
      colorsWrap.appendChild(colorRed);
      const colorYellow = document.createElement('div');
      colorYellow.classList.add('tools-color-item');
      colorYellow.style.backgroundColor = '#FFFF00';
      colorYellow.addEventListener('click', this.currentColorSecondary);
      colorsWrap.appendChild(colorYellow);
      const colorGreen = document.createElement('div');
      colorGreen.classList.add('tools-color-item');
      colorGreen.style.backgroundColor = '#008000';
      colorGreen.addEventListener('click', this.currentColorSecondary);
      colorsWrap.appendChild(colorGreen);
      const colorOrange = document.createElement('div');
      colorOrange.classList.add('tools-color-item');
      colorOrange.style.backgroundColor = '#FFA500';
      colorOrange.addEventListener('click', this.currentColorSecondary);
      colorsWrap.appendChild(colorOrange);
      colorSelect.appendChild(colorsWrap);
    };
    this.selectColorPrimary = () => {
      const colorSelect = document.querySelector('.tools-colorSelect');
      const colorsWrap = document.createElement('div');
      const primaryColor = document.querySelector('.tools-primaryColor');
      primaryColor.classList.add('selecting');
      colorsWrap.classList.add('tools-colorsWrap');
      const colorRed = document.createElement('div');
      colorRed.classList.add('tools-color-item');
      colorRed.style.backgroundColor = '#FF0000';
      colorRed.addEventListener('click', this.currentColorPrimary);
      colorsWrap.appendChild(colorRed);
      const colorYellow = document.createElement('div');
      colorYellow.classList.add('tools-color-item');
      colorYellow.style.backgroundColor = '#FFFF00';
      colorYellow.addEventListener('click', this.currentColorPrimary);
      colorsWrap.appendChild(colorYellow);
      const colorGreen = document.createElement('div');
      colorGreen.classList.add('tools-color-item');
      colorGreen.style.backgroundColor = '#008000';
      colorGreen.addEventListener('click', this.currentColorPrimary);
      colorsWrap.appendChild(colorGreen);
      const colorOrange = document.createElement('div');
      colorOrange.classList.add('tools-color-item');
      colorOrange.style.backgroundColor = '#FFA500';
      colorOrange.addEventListener('click', this.currentColorPrimary);
      colorsWrap.appendChild(colorOrange);
      colorSelect.appendChild(colorsWrap);
    };
    this.add = () => {
      const toolsWrap = document.querySelector('.tools');
      const colorSelect = document.createElement('div');
      colorSelect.classList.add('tools-colorSelect');
      toolsWrap.appendChild(colorSelect);
      const primaryColor = document.createElement('div');
      primaryColor.classList.add('tools-primaryColor');
      primaryColor.addEventListener('click', this.selectColorPrimary);
      colorSelect.appendChild(primaryColor);
      const secondaryColor = document.createElement('div');
      secondaryColor.classList.add('tools-secondaryColor');
      secondaryColor.addEventListener('click', this.selectColorSecondary);
      colorSelect.appendChild(secondaryColor);
    };
  }
}
