import './item-frame.css';

export default class Frame {
  constructor(data) {
    this.data = null;
    this.number = null;
    this.current = data || true;
    this.link = null;
    this.coords = [];
    this.positionY = null;
    this.isMouseDown = false;
    this.deltaY = null;
    this.mouseDown = (e) => {
      this.isMouseDown = true;
      const currentFrame = document.querySelector('.frame-current');
      this.deltaY = e.pageY - currentFrame.style.top;
      currentFrame.addEventListener('mousemove', this.mouseMove.bind(this));
      currentFrame.addEventListener('pointerout', this.mouseUp.bind(this));
      // eslint-disable-next-line no-console
      console.log('mouseDown ---', e.pageY, this.positionY);
    };
    this.mouseUp = () => {
      const currentFrame = document.querySelector('.frame-current');
      currentFrame.removeEventListener('mousemove', this.mouseMove.bind(this));
      this.isMouseDown = false;
      this.deltaY = null;
      // this.toBeCurrent();
    };
    this.mouseMove = (e) => {
      if (this.isMouseDown === true) {
        const currentFrame = document.querySelector('.frame-current');
        // eslint-disable-next-line prefer-template
        currentFrame.style.top = (e.pageY - this.deltaY) + 'px';
        // eslint-disable-next-line no-console
        console.log('moseMove Pos-Y ==', e.pageY, this.deltaY, currentFrame.style.top);
        if ((e.pageY - this.deltaY) > 20) {
          const newData1 = this.data[this.number].coords;
          const newData2 = this.data[this.number + 1].coords;
          this.data[this.number].coords = [];
          this.data[this.number + 1].coords = [];
          newData2.forEach((item) => {
            this.data[this.number].coords.push(item);
          });
          newData1.forEach((item) => {
            this.data[this.number + 1].coords.push(item);
          });
          this.toBeCurrent();
          // eslint-disable-next-line no-console
          console.log('>>>>>>>>>>', currentFrame.style.top, this.data, newData1, newData2);
        }
        if ((e.pageY - this.deltaY) < -10) {
          const newData1 = this.data[this.number].coords;
          const newData2 = this.data[this.number - 1].coords;
          this.data[this.number].coords = [];
          this.data[this.number - 1].coords = [];
          newData2.forEach((item) => {
            this.data[this.number].coords.push(item);
          });
          newData1.forEach((item) => {
            this.data[this.number - 1].coords.push(item);
          });
          this.toBeCurrent();
          // eslint-disable-next-line no-console
          console.log('<<<<<<<<<<<<<<', currentFrame.style.top, this.data, newData1, newData2);
        }
      }
    };
    this.add = (dataM, num, link) => {
      this.link = link;
      this.data = dataM;
      this.number = num;
      const frameManager = document.querySelector('.frameManager');
      const frame = document.createElement('div');
      frame.classList.add('frame-item');
      frame.addEventListener('mousedown', this.toBeCurrent.bind(this));
      frame.addEventListener('mousedown', this.mouseDown.bind(this));
      frame.addEventListener('mouseup', this.mouseUp.bind(this));
      frameManager.appendChild(frame);
      this.positionY = frame.offsetTop;
      // eslint-disable-next-line no-console
      // console.log('add frame', this.data);
      const numberFrameDiv = document.createElement('div');
      numberFrameDiv.classList.add('frame-item_numberDiv');
      numberFrameDiv.innerHTML = num + 1;
      frame.appendChild(numberFrameDiv);
      const btnDeleteFrame = document.createElement('div');
      btnDeleteFrame.classList.add('frame-item_btnDeleteFrame');
      // btnDeleteFrame.classList.add('display-none');
      btnDeleteFrame.innerHTML = '<i class="far fa-trash-alt"></i>';
      btnDeleteFrame.addEventListener('mousedown', this.deleteFrame.bind(this));
      frame.appendChild(btnDeleteFrame);
      // frame.addEventListener('mouseover', this.focus);
      const btnDuplicateFrame = document.createElement('div');
      btnDuplicateFrame.classList.add('frame-item_btnDuplicateFrame');
      btnDuplicateFrame.innerHTML = '<i class="far fa-copy"></i>';
      btnDuplicateFrame.addEventListener('mousedown', this.toBeCurrent.bind(this));
      btnDuplicateFrame.addEventListener('mousedown', this.link.duplicateFrame.bind(link));
      frame.appendChild(btnDuplicateFrame);

      const canv = document.createElement('canvas');
      canv.width = 450;
      canv.height = 450;
      // eslint-disable-next-line prefer-template
      canv.classList.add('frame-item_canvas', 'canvas' + this.number);
      frame.appendChild(canv);
      if (this.current === true) {
        frame.classList.add('frame-current');
      }
    };
  }

  // eslint-disable-next-line class-methods-use-this
  toBeCurrent() {
    this.data.map((item, index) => {
      if (item.current !== false) {
        this.data[index].current = false;
      }
      this.data[this.number].current = true;
      this.link.render();
      // eslint-disable-next-line no-console
      console.log('toBeCurrent', this.number);
      return item;
    });
    this.data.canvasItemLink.clearCanvas();
    this.data.canvasItemLink.viewCurrentFrameInCanvas();
  }

  deleteFrame() {
    this.data.splice(this.number, 1);
    localStorage.clear();
    const frameManager = document.querySelector('.frameManager');
    const btnAddNewFrame = document.querySelector('.frameManager-btn_addNew');
    frameManager.innerHTML = '';
    frameManager.appendChild(btnAddNewFrame);
    this.link.render();
    // eslint-disable-next-line no-console
    console.log('deleteFrame-item', this);
  }

  // eslint-disable-next-line class-methods-use-this
  viewCurrentFrameInFrameCanv(index) {
    // eslint-disable-next-line no-console
    // console.log('replay', this.data[index].coords);
    // eslint-disable-next-line prefer-template
    const className = ('.canvas' + index);
    this.data[index].coords.map((itemC) => {
      itemC.drawFrame(className);
      // eslint-disable-next-line no-console
      // console.log('replay', className, itemC[0]);
      return itemC;
    });
  }
}
