export default class Rect {
  constructor(color, x, y, width, height) {
    this.color = color; //  
    this.x = x; //  
    this.y = y; //  
    this.width = width; // 
    this.height = height; // 
    this.number = null;
    //      
    this.draw = () => {
      const canv = document.querySelector('.canvas-item');
      const ctx = canv.getContext('2d');
      ctx.fillStyle = this.color;
      ctx.fillRect(this.x, this.y, this.width, this.height);
    };
    this.drawFrame = (clas) => {
      // const number = num;
      const canv = document.querySelector(clas);
      const ctx = canv.getContext('2d');
      ctx.fillStyle = this.color;
      ctx.fillRect(this.x, this.y, this.width, this.height);
      // eslint-disable-next-line no-console
      // console.log('canv-------------', canv);
    };
    this.drawPreview = () => {
      // const number = num;
      const canv = document.querySelector('.preview-canvas');
      const ctx = canv.getContext('2d');
      ctx.fillStyle = this.color;
      ctx.fillRect(this.x, this.y, this.width, this.height);
      // eslint-disable-next-line no-console
      // console.log('canv-------------', canv);
    };
  }
}
